<script>
import Header from '@/components/header'
import Footer from '@/components/footer'
import Menu from '@/components/menu'
import Menus from '@/components/menus'
import { getCityList } from "@/request/api"

export default {
    name: 'Citys',
    components:{
        Header,
        Footer,
        Menu,
        Menus,
    },
    data(){
        return {
            page: 1,
            limit: 6,
            currentPage:1,
            cityList:[],
            total: 0
        }
    },
    created() {
      this.getCities()
    },
    mounted(){
        this.returnTop();
    },
    methods:{
        getCities() {
          getCityList({
            page: this.page,
            limit: this.limit
          }).then(res => {
            this.cityList = res.data.list
            this.total = res.data.count
          }).catch(() => {})
        },
        returnTop(){
            document.querySelector("#contents").scrollIntoView(true);  
        },
        changePage(page){
            let _this = this;
            switch(page){
                case 'up':
                    if(_this.currentPage != 1 ){
                        _this.currentPage = _this.currentPage - 1;
                    }
                    break;
                case 'next':
                    if(_this.currentPage != 4 ){
                        _this.currentPage = _this.currentPage + 1;
                    }
                    break;
                default:
                    _this.currentPage = Number(page);
                    break;

            }
        }
    }
}
</script>

